<template>
  <div id="box">
    <!-- <header>
        <div class="back">

        </div>
        <div class="header-title">
            {{title}}
        </div>
        <div class="header-more"></div>
    </header> -->
    <div class="content">
        <div class="section">
            <div class="item noneBor">
                <label for="bulkTitle">
                    团购标题<span >*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="text" v-validate="'required'" placeholder="请输入团购标题" name="bulkTitle" id="bulkTitle" v-model="bulkTitle"/>
                    <i class="tip" v-show="errors.has('bulkTitle')">{{errors.first('bulkTitle')}}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    {{tgtxt}}<span>*</span>
                </label>
                <span class="flex-box">
                    <div class="goods-img" :data-id="bulkImg.id">
                        <img :src="bulkImg.logo">
                        <span>{{bulkImg.name}}</span>
                    </div>
                    <div class="goods-img" v-if="Type==1" @click="openAlertGoods.key=true">
                        <img src="../assets/icon-upimg.png">
                    </div>
                </span>
            </div>
            <div class="item">
                <label>
                    团购时间<span>*</span>
                </label>
                <span class="goods-fill-in start">
                    <div class="fle">
                        <button @click="Type==3?rt():startTime()" :data-date="st">{{starttime}}</button>
                    </div>
                    <div class="slimw fle">
                        至
                    </div>
                    <div class="fle">
                        <button class="right" @click="Type==3?rt():endTime()" :data-date="et">{{endtime}}</button>
                    </div>
                    <i class="btomr"></i>
                </span>
            </div>
            <div class="item noneBor">
                <label for="bulkMsg">
                    团购描述<span>*</span>
                </label>
                <span>
                    <textarea :disabled="Type==3?true:false" v-validate="'required|max:300'" name="bulkMsg"  id="bulkMsg" placeholder="请输入团购描述" cols="30" rows="3" v-model="bulkMsg"></textarea>
                    <i class="tip" v-show="errors.has('bulkMsg')">{{ errors.first('bulkMsg') }}</i>
                </span>
            </div>
        </div>
        <div class="section">
             <div class="item">
                <label for="goodPrice">
                    商品原价<span>*</span>
                </label>
                <span>
                    <input disabled type="number" name="goodPrice" id="goodPrice" placeholder="" v-model="goodPrice"/>
                </span>
            </div>
            <div class="item">
                <label for="bulkPrice">
                    团购价格<span>*</span>
                </label>
                <span>
                    <input disabled  type="number" name="bulkPrice" id="bulkPrice" placeholder="" v-model="bulkPrice"/>
                </span>
            </div>
            <div class="item">
                <label for="bulkManNum">
                    拼团人数<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="number" v-validate="'required|numeric'" name="bulkManNum" id="bulkManNum" placeholder="请输入拼团人数" v-model="bulkManNum"/>
                    <i class="tip" v-show="errors.has('bulkManNum')">{{errors.first('bulkManNum')}}</i>
                </span>
            </div>
            <div class="item nb">
                <label for="limitBuy">
                    限购数量<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="number" v-validate="'required|numeric'" name="limitBuy" id="limitBuy" placeholder="请输入限购数量" v-model="limitBuy"/>
                    <i class="tip" v-show="errors.has('limitBuy')">{{errors.first('limitBuy')}}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item nb" v-show="Type!=3">
                <label>
                    批量填充
                </label>
                <span class="goods-fill-in">
                    <div>
                        <input ref='price' type="number" v-validate="'required|num'" name="price" id="price" placeholder="填写价格" v-model="price"/>
                    </div>
                    <div>
                        <input ref='stock' type="number" v-validate="'required|numeric'" placeholder="填写库存" name="stock" id="stock" v-model="stock"/>
                    </div>
                    <div>
                        <button class="input" @click="pushFn">确定</button>
                    </div>
                </span>
            </div>
            <div class="item nb noMin" v-show="Type!=3">
                <label>
                    
                </label>
                <span class="goods-fill-in">
                    <i class="tip" v-show="errors.has('price')">{{errors.first('price')}}</i>
                    <i class="tip" v-show="errors.has('stock')">{{errors.first('stock')}}</i>
                </span>
            </div>
            <div class="item noMin">
                <table class="table" v-if="thead.length>0">
                    <thead>
                        <tr>
                            <th v-for="(item,i) in thead" :key="i">{{item.specificationName}}</th>
                            <th>售价</th>
                            <th>商品库存</th>
                            <th>团购价</th>
                            <th>团购库存</th>
                        </tr>
                    </thead>
                    <tbody id="skuTbody">
                        <tr v-for="(item,i) in tbody" :key="i" :data-id="item.id||''" :data-skuId="item.skuId||''">
                            <td v-for="(jtem,j) in item.skuValues" :key="j">{{jtem.specificationValue}}</td>
                            <td class="itemPrice">{{item.price}}</td>
                            <td class="itemPrice">{{item.stockNum}}</td>
                            <template v-if="Type==1">    
                                <td><input type="number" class="priceInput" name="priceInput" value="" @blur.stop="uppfn($event,item)"/></td>
				                <td><input type="number" class="stockInput" name="stockInput" value="" @blur.stop="upfn($event,item)"/></td>
                            </template>
                            <template v-if="Type!=1">    
                                <td><input :disabled="Type==3?true:false" ref="priceInput" type="button" class="priceInput" name="priceInput" :value="item.groupPrice" @blur.stop="uppfn($event,item)" @click="uppCfn($event)"/></td>
				                <td><input :disabled="Type==3?true:false" ref="stockInput" type="button" class="stockInput" name="stockInput" :value="item.stockNum" @blur.stop="upfn($event,item)" @click="upCfn($event)"/></td>
                            </template>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-if="Type==1" class="button" @click="ok">确定</button>
                <button v-if="Type==2" class="button" @click="ok">保存</button>
            </div>
        </div>
    </div>
    <alert-goods 
        :init="openAlertGoods" 
        @setlist="getGood" 
        :type="Type"
        :id="id"
        :token="token"
        :taid="taid">
    </alert-goods>
    <!-- 开始时间 -->
    <mt-datetime-picker
        ref="date"
        type="date"
        :startDate="new Date('2010-01-01')"
        :endDate="endDate"
        v-model="startTime1"
        @confirm="handStrat"
        >
    </mt-datetime-picker>
    <!-- 结束时间 -->
    <mt-datetime-picker
        ref="date1"
        type="date"
        :endDate="new Date('2050-01-01')"
        :startDate="startDate"
        v-model="endTime1"
        @confirm="handEnd"
        >
    </mt-datetime-picker>
  </div>
</template>
<script>
    //import AlertGoods from './component/AlertGoods.vue';
    let AlertGoods = resolve => {
        require.ensure(['./component/AlertGoods.vue'], () => {
            resolve(require('./component/AlertGoods.vue'))
        })
    }
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    import { Validator } from 'vee-validate';
    // import { Indicator } from 'mint-ui';
    import _$ from 'expose-loader?underscore!underscore';
    export default{
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空",
                        numeric:(field)=>field+"不是正整数"
                    },
                    attributes:{
                        bulkTitle:"团购标题",
                        bulkMsg:'团购描述',
                        bulkManNum:'拼团人数',
                        limitBuy:'限购数量',
                        price:'价格',
                        stock:'库存'
                    }
                }
            };
            Validator.localize(dictionary);
            Validator.extend('num', {
                getMessage: field => field + '不是数字或小于0',
                validate:value => /^(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*))$/.test(value)
            });
            //初始化
            let type=null;
            if(this.$route.query.type!=undefined){
                type=parseInt(this.$route.query.type);
            }
            if(this.$route.query.id!=undefined){
                this.id=parseInt(this.$route.query.id);
            }
            if(this.$route.query.taid!=undefined){
                this.taid=parseInt(this.$route.query.taid);
            }
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            //全局Type
            this.Type=type;
            this.title="添加团购";
            if(this.Type==Exotic.operationType.update||this.Type==Exotic.operationType.detail){
                if(this.Type==Exotic.operationType.update){
                    this.title="修改团购";
                }else if(this.Type==Exotic.operationType.detail){
                    this.title="查看团购";
                }
                //改变页面title
                var param = {
                    groupBuyId: this.id
                };
                that.MintUi.Indicator.open({
                    text: '加载中...',
                    spinnerType: 'fading-circle'
                });
                Exotic.ajaxData(that.token,Exotic.urlMap.groupbuy.detail, param, 'get',function(res) {
                    let groupBuy = res.groupBuy;
                    console.log(res);
                    //处理开始时间、结束时间
                    that.et=groupBuy.endTime;
                    that.st=groupBuy.startTime;                    
                    that.starttime=com.getTimes(groupBuy.startTime+'','YY-MM-DD');
                    that.endtime=com.getTimes(groupBuy.endTime+'','YY-MM-DD');
                    that.startTime1=new Date(groupBuy.startTime); 
                    that.endTime1=new Date(groupBuy.endTime);
                    // //处理活动标题
                    that.bulkTitle=groupBuy.title;
                    
                    //处理商品展示
                    that.bulkImg={
                        id:groupBuy.id,
                        name:groupBuy.productName,
                        logo:Exotic.imageUrl+groupBuy.thumbImage,
                        thumbnail:groupBuy.thumbImage,
                        price:groupBuy.groupPrice
                    }
                    that.tgtxt='团购商品';
                    //处理团购描述
                    that.bulkMsg=groupBuy.description;
                    //处理skus
                    //渲染sku表thead
                    that.thead=res.skus[0].skuValues;
                    //渲染sku表tbody
                    that.tbody=res.skus;
                    //循环遍历最大价格
                    var maxPrice = res.skus[0].price;
                    _$.each(res.skus, function(item,i) {
                        if(item.price > maxPrice) {
                            maxPrice = item.price;
                        }
                    });
                    that.maxPrice=maxPrice;
                    //循环遍历最大库存
                    var maxStock = res.skus[0].stockNum;
                    _$.each(res.skus, function(item,i) {
                        if(item.stockNum > maxStock) {
                            maxStock = item.stockNum;
                        }
                    });
                    that.maxStock=maxStock;
                    //处理商品原价格
                    that.goodPrice=groupBuy.oldPrice;
                    //处理商品团购价
                    that.bulkPrice=groupBuy.groupPrice;
                    //处理拼团人数attendCount
                    that.bulkManNum=groupBuy.attendCount;
                    //处理限购数量
                    that.limitBuy=groupBuy.limitNum;
                    that.MintUi.Indicator.close();
                });
            }
        },
        components:{
            AlertGoods
        },
        data(){
            function getTimes() {
                var date=new Date();
                var year=date.getFullYear();
                var mon = date.getMonth()+1;
                if(mon<10){
                    mon="0"+mon;
                }
                var day = date.getDate();
                if(day<10){
                    day="0"+day;
                }
                return year+'-'+mon+'-'+day;
            }
            let date=getTimes();
            return {
                token:null,
                Type:1,
                title:'添加团购',
                id:null, //团购id
                taid:null, //商圈id
                bulkTitle:'',//团购标题
                bulkMsg:'',//团购描述
                bulkImgIndex:0,//商品图片的默认选中索引
                activityInfoIndex:0,//商品详情默认选中索引
                startDate:new Date('2010-01-01'),//最小日期
                endDate:new Date('2050-01-01'),//最大日期
                st:'',
                et:'',
                starttime:'开始时间',
                endtime:'结束时间',
                startTime1:new Date(),
                endTime1:new Date(),
                goodPrice:'',//商品原价
                stockNum:'',//商品最低库存
                maxPrice:'',//商品最大价格
                maxStock:'',//商品最大库存
                bulkPrice:'',//团购价格
                bulkManNum:'',//拼团人数
                limitBuy:'',//限购数量,
                dis:false,
                tgtxt:'选择商品',
                //选择商品
                openAlertGoods:{
                    key:false,
                    index:0,
                    id:''
                },
                //选择商品列表
                bulkImg:{},
                //表格
                tables:[],
                thead:[],
                tbody:[],
                price:'',
                stock:''
            }
        },
        watch:{
        },
        methods:{
            //开始时间
            startTime(){
                this.$refs.date.open();
            },
            //结束时间
            endTime(){
                this.$refs.date1.open();
            },
            //开始时间确认
            handStrat(res){
                console.log(typeof res);
                this.starttime=com.getTimes(res,'YY-MM-DD');
                this.startDate=res;
                this.st=new Date(res).getTime();
            },
            //结束时间确认
            handEnd(res){
                this.endtime=com.getTimes(res,'YY-MM-DD');
                this.endDate=res;
                this.et=new Date(res).getTime();
            },
            //预览图片
            previewImg(item,i){
                this.imgPreview.key=true;
                this.imgPreview.index=i;
                this.imgPreview.id=item.id;
            },
            //设置封面返回
            indexChange(i){
                this.activityImgIndex=i;
            },
            //弹出防止冒泡
            rt(){
                return true;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                /*return arr.find((item)=>{
                    return item.value == obj.value;
                });*/
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj.value){
                        return arr[i];
                    }
                }
            },
            //团购库存判断
            upfn(ev,item){
                let that=this;
                if(!isNaN(parseInt(ev.target.value))){
                    if(ev.target.value>item.stockNum){
                        Exotic.tip({txt:'团购库存大于商品库存'})
                        ev.target.focus();
                    }else if(ev.target.value<0){
                        Exotic.tip({txt:'团购库存不能小于1'});
                        ev.target.focus();
                    }else{
                        if(that.Type!=1){
                            ev.target.setAttribute('type','button');
                        }
                        that.bulkPrice=ev.target.value;
                    }
                }else{
                    Exotic.tip({txt:'不是数字'});
                    ev.target.focus();
                }
            },
            uppfn(ev,item){
                let that=this;
                if(!isNaN(parseInt(ev.target.value))){
                    if(ev.target.value>item.price){
                        Exotic.tip({txt:'团购价格大于商品库存'})
                        ev.target.focus();
                    }else if(ev.target.value<0){
                        Exotic.tip({txt:'团购价格不能小于1'});
                        ev.target.focus();
                    }else{
                        if(that.Type!=1){
                            ev.target.setAttribute('type','button');
                        }
                        that.bulkPrice=ev.target.value;
                    }
                }else{
                    Exotic.tip({txt:'不是数字'});
                    ev.target.focus();
                }
            },
            upCfn(ev){
                ev.target.setAttribute('type','text');
            },
            uppCfn(ev){
                ev.target.setAttribute('type','text');
            },
            pushFn(){
                let that=this;
                if(that.thead.length>0){
                    if(that.price>that.maxPrice){
                        Exotic.tip({txt:'价格不能大于商品原价'});
                        that.$refs.price.focus();
                    }else if(that.stock>that.maxStock){
                        Exotic.tip({txt:'库存不能大于商品库存'});
                        that.$refs.stock.focus();
                    }else if(that.price<0){
                        Exotic.tip({txt:'价格不能小于0'});
                        that.$refs.price.focus();
                    }else if(that.stock<0){
                        Exotic.tip({txt:'库存不能小于0'});
                        that.$refs.stock.focus();
                    }else if(isNaN(that.price)){
                        Exotic.tip({txt:'价格不是数字'});
                        that.$refs.price.focus();
                    }else if(isNaN(that.stock)){
                        Exotic.tip({txt:'库存不是数字'});
                        that.$refs.stock.focus();
                    }else if(that.stock==''||that.price==''||that.stock==''&&that.price==''){
                        Exotic.tip({txt:'请填写库存和价格'});
                        that.$refs.price.focus();
                    }else{
                        $('.priceInput').val(that.price);
                        $('.stockInput').val(that.stock);
                        that.bulkPrice=that.price;
                    }
                }else{
                    Exotic.tip({txt:'请选择商品'});
                }
            },
            getGood(list){
                let that=this;
                that.MintUi.Indicator.open({
                    text: '加载中...',
                    spinnerType: 'fading-circle'
                });
                this.bulkImg=list;
                var param = {
                    productId: list.id
                };
                Exotic.ajaxData(that.token,Exotic.urlMap.specifitioin.get, param, 'get', function(res) {
                   //打印返回数据
                    console.log(res);
                    //填写商品原价input
                    var price = res.skus[0].price;
                    //循环遍历最小价格
                    _$.each(res.skus, function(item,i) {
                        if(item.price < price) {
                            price = item.price;
                        }
                    });
                    var stockNum = res.skus[0].stockNum;
                    //循环遍历最小库存
                    _$.each(res.skus, function(item,i) {
                        if(item.stockNum < stockNum) {
                            stockNum = item.stockNum;
                        }
                    });
                    that.stockNum=stockNum;
                    that.goodPrice=price;
                    //循环遍历最大价格
                    var maxPrice = res.skus[0].price;
                    _$.each(res.skus, function(item,i) {
                        if(item.price > maxPrice) {
                            maxPrice = item.price;
                        }
                    });
                    that.maxPrice=maxPrice;
                    //循环遍历最大库存
                    var maxStock = res.skus[0].stockNum;
                    _$.each(res.skus, function(item,i) {
                        if(item.stockNum > maxStock) {
                            maxStock = item.stockNum;
                        }
                    });
                    that.maxStock=maxStock;
                    //渲染sku表thead
                    that.thead=res.skus[0].skuValues;
                    //渲染sku表tbody
                    that.tbody=res.skus;
                    //隐藏加载
                    that.MintUi.Indicator.close();
                });
            },
            ok(){
                let that=this;
                //构建specificationContent
                var tbody_trs = $('#skuTbody').find('tr');
                var specificationContent = [];
                var isNotAll = false;
                $.each(tbody_trs, function(i, item) {
                    var specificationContent_item = {};
                    specificationContent_item.id = parseInt($(item).attr('data-id'));
                    specificationContent_item.skuId = parseInt($(item).attr('data-skuId'));
                    specificationContent_item.price = parseFloat($(item).find('.itemPrice').html());
                    specificationContent_item.stockNum = parseInt($(item).find('.stockInput').val());
                    specificationContent_item.groupPrice = parseFloat($(item).find('.priceInput').val());
                    specificationContent.push(specificationContent_item);
                    if($(item).find('.stockInput').val() == '' || $(item).find('.priceInput').val() == '') {
                        isNotAll = true;
                    }
                });
                specificationContent = JSON.stringify(specificationContent);
                //获取团购标题
		        var title =that.bulkTitle;
                //获取商品原价
                var oldPrice = that.goodPrice ? parseFloat(that.goodPrice) : '';
                //获取商品团购价
                var groupPrice =that.bulkPrice? parseFloat(that.bulkPrice):'';
                //获取拼团人数
                var attendCount = that.bulkManNum ? parseFloat(that.bulkManNum) : '';
                //获取限购数量
                var limitNum = that.limitBuy ? parseFloat(that.limitBuy) : '';
                //获取团购描述
                var description = that.bulkMsg;
                if(title == '') {
                    Exotic.tip({txt:"请填写团购标题"});
                    return false;
                }else if(that.bulkImg == '') {
                    Exotic.tip({txt:"请选择商品"});
                    return false;
                }else if(that.st == '') {
                    Exotic.tip({txt:"请选择开始时间"});
                    return false;
                } else if(that.et == '') {
                    Exotic.tip({txt:"请选择结束时间"});
                    return false;
                }else if(description == ''){
                    Exotic.tip({txt:"请填写团购描述"});
                    return false;
                }else if(isNaN(attendCount)){
                    Exotic.tip({txt:"拼团人数不是数字"});
                    return false;
                }else if(attendCount < 2) {
                    Exotic.tip({txt:"拼团人数最少要两个人以上"});
                    return false;
                }else if(isNaN(limitNum)) {
                    Exotic.tip({txt:"限购数量不是数字"});
                    return false;
                }else if(limitNum=="") {
                    Exotic.tip({txt:"请填写限购数量"});
                    return false;
                }else if(isNotAll) {
                    Exotic.tip({txt:"请填写正确商品规格表"});
                    return false;
                }else{
                    if(that.Type==1){
                        var param = {
                            tradeAreaId: that.taid,
                            productId: that.bulkImg.id,
                            title: title,
                            description: description,
                            thumbImage: that.bulkImg.thumbnail.replace(Exotic.imageUrl,''),
                            oldPrice: oldPrice,
                            groupPrice: groupPrice,
                            startTime: that.starttime,
                            endTime: that.endtime,
                            weeks: '',
                            times: '',
                            limitNum: limitNum,
                            attendCount: attendCount,
                            specificationContent: specificationContent
                        };
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        });
                        Exotic.ajaxData(that.token,Exotic.urlMap.groupbuy.add, param, 'post', function(data) {
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"添加团购成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=14";
                            }
                        });
                    }else if(that.Type==2){
                        var param = {
                            groupBuyId: that.id,
                            productId: that.bulkImg.id,
                            title: title,
                            description: description,
                            thumbImage: that.bulkImg.thumbnail.replace(Exotic.imageUrl,''),
                            oldPrice: oldPrice,
                            groupPrice: groupPrice,
                            startTime: that.starttime,
                            endTime: that.endtime,
                            weeks: '',
                            times: '',
                            limitNum: limitNum,
                            attendCount: attendCount,
                            specificationContent: specificationContent
                        };
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        });
                        Exotic.ajaxData(that.token,Exotic.urlMap.groupbuy.update, param, 'post', function(data) {
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"修改团购成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=14";
                            }
                        });
                    }
                    
                }
            },
            save(){
                
            }
        },
        mounted(){
            let that=this;
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
</style>




